.page-header {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  background-color: #ffffff;
  width: 100%;
  display: flex;
  border-bottom: 1px solid #ededed;
}
.page-header__logo {
  display: block;
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
.page-header__logo-image {
  display: block;
}
.page-header__container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.page-header__container-logo {
  width: 280px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 65px;
}
.main-nav {
  position: relative;
  width: 320px;
  margin: 0 auto;
  font-family: "Oswald Medium", "Arial", sans-serif;
}
.page-header__toggle {
  position: relative;
  padding: 0;
  z-index: 2;
  display: block;
  width: 25px;
  height: 19px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.page-header__toggle:focus {
  outline: none;
}
.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}
.page-header__toggle:active::before {
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}
.page-header__container--opened .page-header__toggle {
  position: relative;
}
.page-header__container--opened .page-header__toggle::before,
.page-header__container--opened .page-header__toggle::after {
  content: "";
  position: absolute;
  width: 19px;
  height: 2px;
  background-color: #222222;
}
.page-header__container--opened .page-header__toggle::before {
  top: auto;
  left: auto;
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header__container--opened .page-header__toggle::after {
  transform: rotate(-45deg);
}
.page-header__container--opened .page-header__toggle:active::before,
.page-header__container--opened .page-header__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__link {
  color: #000000;
  padding: 24px 0 20px 0;
  display: block;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.site-list__item {
  text-align: center;
  border-top: 1px solid #ededed;
}
.site-list__item:last-child {
  border-bottom: 1px solid #ededed;
}
@media (max-width: 767px) {
  .main-nav .site-list {
    display: none;
  }
  .page-header__container--nojs .page-header__toggle {
    display: none;
  }
  .page-header__container--nojs .site-list,
  .page-header__container--opened .site-list {
    display: block;
  }
}
