.form-main__container {
  width: 280px;
  margin: 0 auto;
}

.form-main__option--container {
  width: 280px;
  margin: 0 auto;
}

.form-main__begin {
  border: none;
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  font-family: @font;
  color: @black-doublelighten;
}

.form-main__begin-input {
  display: flex;
  border: none;
  padding: 0;
  width: 280px;
  margin: 0 auto;
  flex-direction: column;
}

.form-main__begin-choose {
  display: flex;
  border: none;
  padding: 0;
  margin: 0;
  border-bottom: 1px solid @white-gray;
  border-top: 1px solid @white-gray;
}

.form-main__option {
  list-style: none;
  padding: 0;
}

.form-main__option:first-child {
  margin-top: 26px;
}

.form-main__option:last-child {
  margin-bottom: 29px;
}

.form-main__option-item {
  margin-bottom: 23px;
  margin-left: 46px;
}

.form-main__contact {
  display: flex;
  flex-wrap: wrap;
  border: none;
  padding: 0;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 18px;
  font-family: @font;
  color: @black-doublelighten;
}

.form-main__contact-title {
  font-size: 24px;
  line-height: 24px;
  text-align: left;
  font-family: @font;
  color: @black-lighten;
  margin: 0;
  margin-top: 29px;
  margin-bottom: 32px;
  text-transform: none;
  display: flex;
  position: relative;
}

.form-main__contact-title::after {
  content: "";
  position: absolute;
  width: 99px;
  height: 3px;
  left: 201px;
  background-color: @leaf;
  top: 13px;
}

.form-main__contact-mailinput {
  padding: 13px 0 12px 14px;
  width: 217px;
  border: 2px solid transparent;
  color: @gray;
  font-size: 16px;
  line-height: 18px;
  font-family: @font;
  text-transform: uppercase;
  margin-left: 8px;
  margin-bottom: 15px;
  position: relative;
  outline: 1px solid rgba(205, 205, 205, 0.5);
  height: 45px;
}

.form-main__contact-mailinput--error {
  display: none;
  border: 2px solid #ff8282;
}

.form-main__contact-mail,
.form-main__contact-phone {
  position: relative;
}

.form-main__contact-mail::after {
  content: "";
  position: absolute;
  top: 18px;
  left: 247px;
  width: 18px;
  height: 14px;
  background-image: url("../img/icon-mail.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.form-main__contact-mail:focus::after {
  background-image: url("../img/icon-mail-active.svg");
}

.form-main__contact-mailinput--error::after {
  content: "";
  position: absolute;
  top: 18px;
  left: 247px;
  width: 18px;
  height: 14px;
  background-image: url("../img/icon-mail-error.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.form-main__contact-phone::after {
  content: "";
  position: absolute;
  top: 13px;
  left: 247px;
  width: 21px;
  height: 21px;
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.form-main__contact-phone--error::after {
  content: "";
  position: absolute;
  top: 13px;
  left: 247px;
  width: 21px;
  height: 21px;
  background-image: url("../img/icon-phone-error.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.form-main__contact-phone:focus::after {
  background-image: url("../img/icon-phone-active.svg");
}

.form-main__contact-phoneinput {
