.filter-input-radio + label::before {
  content: "";
  left: -46px;
  top: -4px;
  position: absolute;
  width: 30px;
  height: 30px;
  border: 1px solid #ededed;
  border-radius: 50%;
}

.filter-input-radio + label:hover::before {
  border: 1px solid #bababa;
}

.filter-input-radio + label:focus::before {
  border: 1px solid #bababa;
  opacity: 0.3;
}

.filter-input-radio:checked + label::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background-color: @leaf;
  top: 5px;
  left: -37px;
  border-radius: 50%;
}

.filter-input-radio:checked + label:focus::after {
  opacity: 0.3;
}

.filter-input-checkbox + label::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  left: -46px;
  top: -4px;
  border: 1px solid #ededed;
}

.filter-input-checkbox + label:hover::before {
  border: 1px solid #bababa;
}

.filter-input-checkbox + label:focus::before {
  border: 1px solid #bababa;
  opacity: 0.3;
}

.filter-input-checkbox:checked + label::before {
  content: "";
  position: absolute;
  background-image: url("../img/tick-svg.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.form-main__option-name {
  position: relative;
}

.form-main__option-name:focus {
  opacity: 0.3;
}

.form-main__aditional-name {
  position: relative;
}

.form-main__aditional-name:focus {
  opacity: 0.3;
}

