.page-footer {
  background-color: #f2f2f2;
  color: @gray;
  width: 100%;
  height: 236px;
}

.page-footer__container {
  width: 280px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-footer__logo-image {
  margin-top: 34px;
  margin-bottom: 25px;
}

.page-footer__logo:hover {
  opacity: 0.8;
}

.page-footer__logo:active {
  opacity: 0.6;
}

.page-footer__social {
  width: 100%;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid  rgba(0, 0, 0, 0.1);
  display: flex;

  height: 63px;
}

.social__list {
  padding-left: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 73px;
}

.social__item {
  list-style: none;
  text-align: center;
  margin-right: 34px;
}

.social__item:nth-child(2) {
  margin-right: 40px;
}

.social__link {
  color: #666666;
}

.social__link:hover {
  color: @leaf;
}

.social__link:active {
  color: @leaf;
  opacity: 0.3;
}

.page-footer__link {
  font-family: @arial;
  font-size: 16px;
  line-height: 16px;
  color: @gray;
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  height: 74px;
}

.page-footer__link:active {
  opacity: 0.3;
}

.page-footer__link::after {
  content: "";
  width: 27px;
  height: 34px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url("../img/htmlacademy.svg");
  display: block;
}

.page-footer__link:hover::after {
  background-image: url("../img/htmlacademy-active.svg");
}

.page-footer__link:active::after {
  background-image: url("../img/htmlacademy-active.svg");
}

.social__link-icon {
  fill: currentColor;
}

@media (min-width: @tablet-width) {
  .page-footer {
    width: @tablet-width;
    height: 144px;
    display: flex;
    align-items: center;
  }

  .page-footer__container {
    width: 708px;
    margin: 0 auto;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .page-footer__logo-image {
    margin: 0;
  }

  .page-footer__social {
    width: 130px;
    height: 63px;
    border: none;
    justify-content: center;
  }

  .social__item {
    margin-right: 28px;
  }

  .social__list {
    margin: 0;
    width: 100px;
  }

  .social__item:nth-child(2) {
    margin-right: 28px;
  }

  .page-footer__link {
    width: 162px;
    height: 74px;
  }

  .social__link-icon--vkontaktebig {
    width: 30px;
    height: 16px;
  }

  .social__link-icon--instagrambig {
    width: 23px;
    height: 23px;
  }

  .social__link-icon--fbbig {
    width: 14px;
    height: 28px;
  }
}

@media (min-width: @desktop-width) {
  .page-footer {
    width: @desktop-width;
    height: 150px;
  }

  .page-footer__container {
    width: 1220px;
  }

  .page-footer__logo-image {
    margin: 0;
  }
}
