.page-header {
  font-family: @font;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  background-color: @white;
  width: 100%;
  display: flex;
  border-bottom: 1px solid @white-gray;
}

.page-header__logo {
  display: block;
}

.page-header__logo:hover {
  opacity: 0.8;
}

.page-header__logo:active {
  opacity: 0.6;
}

.page-header__logo-image {
  display: block;
}

.page-header__container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.page-header__container-logo {
  width: 280px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 65px;
}

.main-nav {
  position: relative;
  width: 320px;
  margin: 0 auto;
  font-family: @font-bold;
}

.page-header__toggle {
  position: relative;
  padding: 0;
  z-index: 2;
  display: block;
  width: 25px;
  height: 19px;
  cursor: pointer;
  border: none;
  background-color: @white;
}

.page-header__toggle:focus {
  outline: none;
}

.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}

.page-header__toggle:active::before {
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}

.page-header__container--opened .page-header__toggle {
  position: relative;
}

.page-header__container--opened .page-header__toggle::before,
.page-header__container--opened .page-header__toggle::after {
  content: "";
  position: absolute;
  width: 19px;
  height: 2px;
  background-color: #222222;
}

.page-header__container--opened .page-header__toggle::before {
  top: auto;
  left: auto;
  transform: rotate(45deg);
  box-shadow: none;
}

.page-header__container--opened .page-header__toggle::after {
  transform: rotate(-45deg);
}

.page-header__container--opened .page-header__toggle:active::before,
.page-header__container--opened .page-header__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.main-nav__link {
  color: @black;
  padding: 24px 0 20px 0;
  display: block;
}

.main-nav__link:hover {
  opacity: 0.6;
}

.main-nav__link:active {
  opacity: 0.3;
}

.site-list__item {
  text-align: center;
  border-top: 1px solid @white-gray;
}

.site-list__item:last-child {
  border-bottom: 1px solid @white-gray;
}

@media (max-width: @mobile-width-only) {
  .main-nav .site-list {
    display: none;
  }

  .page-header__container--nojs .page-header__toggle {
    display: none;
  }

  .page-header__container--nojs .site-list,
  .page-header__container--opened .site-list {
    display: block;
  }
}

@media (min-width: @tablet-width) {
  .page-header {
    font-size: 18px;
    line-height: 25px;
    width: @tablet-width;
    border-bottom: none;
  }

  .page-header__logo {
    margin: 0;
    margin-right: auto;
  }

  .page-header__logo-image {
    width: 174px;
    height: 59px;
  }

  .page-header__container {
    width: 708px;
    height: 62px;
    margin: 0 auto;
    margin-bottom: 12px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    margin-top: 20px;
  }

  .page-header__container-logo {
    align-items: flex-end;
    min-height: 100%;
  }

  .page-header__toggle {
    display: none;
  }

  .main-nav {
    width: 454px;
    margin: 0;
    font-family: @font;
  }

  .main-nav__list {
    display: flex;
    justify-content: space-between;
  }

  .site-list__item {
    text-align: center;
    padding: 0;
    border: none;
  }

  .site-list__item--active {
    position: relative;
  }

  .site-list__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -9px;
    left: 0;
    height: 2px;
    background: #63b738;
  }

  .main-nav__link {
    color: @black-doublelighten;
    padding: 0;
  }

  .site-list__item:last-child {
    border-bottom: none;
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    font-size: 20px;
    color: @white;
    width: @desktop-width;
    min-height: 120px;
    z-index: 1;
    position: relative;
    background-color: transparent;
  }

  .page-header__container {
    width: 1220px;
    height: 80px;
    margin-bottom: 75px;
    align-items: flex-end;
  }

  .page-header__container-logo {
    margin: 0;
  }

  .page-header__logo-image {
    width: 203px;
    height: 60px;
  }

  .page-header__logo {
    position: absolute;
    top: 55px;
  }

  .main-nav {
    width: 477px;
  }

  .main-nav__list {
    align-items: center;
  }

  .main-nav__list--index .site-list__item--active::after {
    background: @white;
  }

  .main-nav__list--index a {
    color: @white;
    display: block;
  }
}
