.aditional {
  width: 100%;
}

.aditional__container {
  width: 280px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  margin-bottom: 34px;
}

.aditional__name-container {
  width: 280px;
  margin: 0 auto;
}

.aditional__name {
  font-size: 24px;
  line-height: 24px;
  text-align: left;
  font-weight: normal;
  margin: 29px 0 33px 0;
  position: relative;
}

.aditional__name::after {
  content: "";
  position: absolute;
  width: 53px;
  height: 3px;
  left: 247px;
  background-color: @leaf;
  top: 13px;
}

.aditional__list {
  padding: 0;
  margin: 0;
  margin-bottom: 39px;
}

.aditional__item {
  border-top: 1px solid @white-gray;
  list-style: none;
}

.aditional__item:last-child {
  border-bottom: 1px solid @white-gray;
}

.aditional__title {
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-align: left;
  margin: 14px 0 13px 0;
  color: @black-doublelighten;
}

.aditional__table {
  font-size: 14px;
  line-height: 14px;
  font-family: @arial;
  color: @gray;
  margin: 0 0 16px 0;
  border: 0;
  width: 100%;
  border-spacing: 0;
}

.aditional__table-item {
  text-align: left;
  padding: 0;
}

.aditional__table-value {
  text-align: right;
  padding: 0;
}

.aditional__button {
  padding: 9px 0 9px 0;
  font-size: 16px;
  line-height: 20px;
  width: 100%;
  margin-bottom: 17px;
}

.aditional__gift {
  height: 289px;
  background: url("../img/gift-photo-mobile@1x.jpg");
  background-color: @leaf;
  background-repeat: no-repeat;
  color: @white;
  font-family: @arial;
  line-height: 20px;
  display: flex;
  justify-content: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
    background-size: auto 289px;
  }

  @media (min-resolution: 3dppx) {
    background-image: url("../img/gift-photo-mobile-jpg@3x.png");
    background-size: auto 289px;
  }
}

.aditional__option {
  padding: 0;
  margin: 0;
  padding-top: 188px;
  width: 60%;
  text-align: center;
  position: relative;
}

.aditional__option::before {
  content: "";
  width: 56px;
  height: 61px;
  background-image: url("../img/icon-gift.svg");
  background-repeat: no-repeat;
  position: absolute;
  top: 65px;
  left: 53px;
}


@media (min-width: @tablet-width) {
  .aditional__container {
    width: 708px;
    margin-bottom: 76px;
  }

  .aditional__name-container {
    width: 708px;
  }

  .aditional__name {
    font-size: 36px;
    line-height: 36px;
    margin: 0 0 82px 37px;
  }

  .aditional__name::before {
    content: "";
    position: absolute;
    background-color: @leaf;
    width: 30px;
    height: 3px;
    left: -67px;
    top: 22px;
  }

  .aditional__name::after {
    width: 326px;
    left: 375px;
    top: 22px;
  }

  .aditional__item {
    border-left: 1px solid @white-gray;
    border-right: 1px solid @white-gray;
    padding: 24px 36px 30px 40px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .aditional__list {
    margin-bottom: 69px;
  }

  .aditional__title {
    font-size: 20px;
    line-height: 20px;
    margin: 0;
    margin-bottom: 20px;
    width: 30%;
  }

  .aditional__table {
    font-size: 16px;
    line-height: 20px;
    margin: 0;
    margin-bottom: 20px;
    border: 0;
    width: 40%;
  }

  .aditional__table-value {
    width: 46px;
    padding-top: 4px;
  }

  .aditional__table-item {
    text-align: right;
    padding-right: 48px;
    padding-top: 4px;
  }

  .aditional__button {
    margin-bottom: 0;
    padding: 10px 0;
  }

  .aditional__gift {
    height: 200px;
    background: url("../img/gift-photo-tablet@1x.jpg");
    background-repeat: no-repeat;
    color: @white;
    display: flex;
    justify-content: space-between;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
      background-size: auto 200px;
    }

    @media (min-resolution: 3dppx) {
      background-image: url("../img/gift-photo-tablet-jpg@3x.png");
      background-size: auto 200px;
    }
  }

  .aditional__option {
    padding: 73px 0 0 0;
    margin-left: 424px;
    width: 165px;
    text-align: left;
    position: relative;
  }

  .aditional__option::before {
    left: -333px;
  }
}

@media (min-width: @desktop-width) {
  .aditional__container {
    width: 1220px;
    margin-bottom: 86px;
    flex-direction: row;
    justify-content: space-between;
  }

  .aditional__name-container {
    width: 1220px;
  }


  .aditional__name {
    margin: 0 0 79px 0;
  }

  .aditional__name::after {
    width: 956px;
    left: 374px;
    top: 21px;
  }

  .aditional__name::before {
    width: 70px;
    left: -109px;
    top: 21px;
  }

  .aditional__list {
    width: 895px;
    margin-bottom: 0;
  }

  .aditional__item {
    border-left: none;
    border-right: none;
    padding: 16px 0 17px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    align-items: center;
    margin-left: 2px;
  }

  .aditional__title {
    margin: 0;
    width: 160px;
    margin-right: 159px;
  }

  .aditional__table {
    margin: 0;
    margin-right: 24px;
    width: 289px;
  }

  .aditional__table-item {
    text-align: left;
    padding: 6px 100px 0 0;
    width: 0;
  }

  .aditional__table-value {
    text-align: left;
    padding: 0;
    padding-top: 6px;
    width: 45px;
  }

  .aditional__button {
    margin-bottom: 0;
    width: 245px;
    padding: 9px 0 9px 2px;
  }

  .aditional__gift {
    height: 289px;
    width: 245px;
    background: url("../img/gift-photo-desktop@1x.jpg");
    background-repeat: no-repeat;
    color: @white;
    font-family: @arial;
    line-height: 20px;
    display: flex;
    justify-content: center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
      background-size: 245px 289px;
    }
  }

  .aditional__option {
    padding: 0;
    margin: 0;
    padding-top: 187px;
    width: 70%;
    text-align: center;
    position: relative;
  }

  .aditional__option::before {
    content: "";
    width: 56px;
    height: 61px;
    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
    position: absolute;
    top: 65px;
    left: 59px;
  }
}
