.slider {
  width: 100%;
  background-color: #f2f2f2;
}

.slider__container {
  width: 280px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.slider__name {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 23px;
  font-family: @font;
  font-size: 36px;
  line-height: 60px;
  padding-top: 16px;
}

.slider__description {
  margin: 0 14px 27px 0;
  font-family: @arial;
  font-size: 14px;
  line-height: 18px;
  color: @gray;
}

.slider__weight,
.slider__days {
  width: 124px;
  height: 56px;
  padding: 0;
  margin: 0;
  font-family: @arial;
  font-size: 12px;
  line-height: 12px;
  color: @gray;
  text-align: center;
  border: 1px solid #cdcdcd;
  position: relative;
}

.slider__weight {
  margin-right: 32px;
}

.slider__weight-extra {
  position: absolute;
  background-color: #f2f2f2;
  width: 87px;
  padding: 0 17px 0 16px;
  bottom: -17px;
  left: 18px;
}

.slider__days-extra {
  position: absolute;
  background-color: #f2f2f2;
  width: 87px;
  padding: 0 15px 0 15px;
  bottom: -17px;
  left: 18px;
}

.slider__value {
  font-family: @font;
  font-size: 24px;
  line-height: 24px;
  color: @black-lighten;
  text-align: center;
  margin-top: 12px;
  text-transform: uppercase;
}

.slider__cost {
  margin: 0 auto;
  margin-top: 34px;
  margin-bottom: 34px;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}

.slider__view {
  display: flex;
  justify-content: space-between;
  padding: 39px 49px 34px 64px;
  align-items: center;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  color: @black-lighten;
}

.slider__view-maximum {
  margin-right: 10px;
}

.slider__scale {
  width: 84px;
  height: 14px;
  background-color: @white;
  border-radius: 6px;
  display: flex;
  align-items: center;
}

.slider__bar {
  width: 35px;
  height: 6px;
  background-color: @leaf;
  margin: 0 0 0 6px;
  border-radius: 3px;
}

.slider__toggle {
  display: none;
}

.slider__first {
  width: 311px;
  height: 232px;
  background: url("../img/before-mobile@1x.png");
  background-size: 311px 232px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/before-mobile@2x.png");
    background-size: 311px 232px;
  }
}

.slider__second {
  display: none;
  width: 311px;
  height: 232px;
  background: url("../img/after-mobile@1x.png");
  background-size: 311px 232px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/after-mobile@2x.png");
    background-size: 311px 232px;
  }
}

@media (min-width: @tablet-width) {
  .slider {
    background-color: @white;
  }

  .slider__container {
    width: 708px;
    margin-bottom: 255px;
  }

  .slider__image--container {
    background-color: #f2f2f2;
    height: 462px;
    position: relative;
    display: flex;
  }

  .slider__name {
    width: 708px;
    margin-top: 0;
    margin-bottom: 62px;
    font-size: 60px;
  }

  .slider__description {
    margin: 0 32px 56px 0;
    font-size: 16px;
    line-height: 24px;
  }

  .slider__weight,
  .slider__days {
    width: 162px;
    height: 70px;
    font-size: 14px;
    line-height: 14px;
  }

  .slider__weight {
    margin-right: 42px;
  }

  .slider__days {
    margin-right: 162px;
  }

  .slider__weight-extra {
    background-color: @white;
    width: 114px;
    padding: 8px 9px 8px 8px;
    bottom: -14px;
    left: 24px;
  }

  .slider__days-extra {
    background-color: @white;
    width: 142px;
    padding: 8px 7px;
    bottom: -14px;
    left: 10px;
  }

  .slider__value {
    font-size: 30px;
    line-height: 37px;
  }

  .slider__cost {
    width: 180px;
    margin: 0;
    margin-top: 10px;
    font-size: 20px;
    line-height: 20px;
    text-align: right;
  }

  .slider__view {
    padding: 15px 100px 70px 100px;
    font-size: 20px;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .slider__view-minimum,
  .slider__view-maximum {
    border-bottom: 1px dotted rgba(0, 0, 0, 0.23);
    padding-bottom: 6px;
  }

  .slider__view-minimum:hover,
  .slider__view-maximum:hover {
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  }

  .slider__view-minimum:active,
  .slider__view-maximum:active {
    border-bottom: none;
  }

  .slider__view-minimum {
    margin-right: 24px;
  }

  .slider__view-maximum {
    margin-left: 21px;
  }

  .slider__scale {
    width: 428px;
    height: 6px;
    background-color: #dcdcdc;
    border-radius: 2.5px;
  }

  .slider__bar {
    display: none;
  }

  .slider__toggle {
    display: block;
    position: absolute;
    top: -16px;
    left: 46%;
    width: 34px;
    height: 34px;
    padding: 0;
    border: 1px solid #cdcdcd;
    background-color: @white;
    border-radius: 50%;
    cursor: pointer;
    z-index: 10;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.15);
  }

  .slider__toggle:hover {
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25);
  }

  .slider__toggle:active {
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.3);
    border: 2px solid @leaf;
  }

  .slider__toggle::after {
    content: "";
    position: absolute;
    top: 12px;
    left: 12px;
    width: 8px;
    height: 8px;
    padding: 0;
    background-color: @leaf;
    border-radius: 50%;
    cursor: pointer;
    z-index: 10;
  }

  .slider__controls {
    position: relative;
  }

  .slider__image--before {
    width: 690px;
    height: 517px;
    position: absolute;
    top: -151px;
    margin-left: 34px;
  }

  .slider__image--after {
    width: 690px;
    height: 517px;
    position: absolute;
    top: -151px;
    margin-left: 34px;
  }

  .slider__image-first {
    width: 50%;
    height: 462px;
  }

  .slider__image-second {
    width: 50%;
    height: 462px;
    background-color: #eaeaea;
  }

  .slider__first {
    width: 338px;
    height: 517px;
    background: url("../img/before-tablet@1x.png");
    background-repeat: no-repeat;
    background-size: 690px 517px;
    position: absolute;
    top: -166px;
    left: 47px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/before-tablet@2x.png");
      background-size: 690px 517px;
    }
  }

  .slider__second {
    display: block;
    width: 353px;
    height: 517px;
    background: url("../img/after-tablet@1x.png");
    background-repeat: no-repeat;
    background-size: 690px 517px;
    position: absolute;
    top: -166px;
    background-position: right;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/after-tablet@2x.png");
      background-size: 690px 517px;
    }
  }
}

@media (min-width: @desktop-width) {
  .slider__wrapper {
    width: 100%;
    margin: 0;
    height: 462px;
    background-color: #f2f2f2;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  .slider__container {
    width: 450px;
    margin: 72px 0 75px 111px;
  }

  .slider__image--container {
    width: 790px;
    height: auto;
  }

  .slider__name {
    width: 1220px;
    padding-top: 3px;
    margin-bottom: 75px;
  }

  .slider__description {
    margin: 0 15px 68px 0;
    width: 435px;
  }

  .slider__weight,
  .slider__days {
    width: 162px;
    height: 72px;
    margin-bottom: 62px;
  }

  .slider__weight {
    margin-right: 42px;
  }

  .slider__days {
    margin-right: 0;
  }

  .slider__weight-extra {
    background-color: #f2f2f2;
  }

  .slider__days-extra {
    background-color: #f2f2f2;
  }

  .slider__cost {
    width: 100%;
    margin-top: 0;
    text-align: left;
  }

  .slider__view {
    padding-top: 0;
    font-size: 20px;
    padding-left: 110px;
  }

  .slider__bar {
    display: none;
  }

  .slider__toggle {
    left: 47%;
  }

  .slider__controls {
    position: relative;
  }

  .slider__image--before {
    width: 690px;
    height: 517px;
    position: absolute;
    top: -151px;
    margin-left: 34px;
  }

  .slider__image--after {
    width: 690px;
    height: 517px;
    position: absolute;
    top: -151px;
    margin-left: 34px;
  }

  .slider__first {
    width: 365px;
    background: url("../img/before-desktop@1x.png");
    top: -190px;
    left: 32px;
    background-size: 690px 517px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/before-desktop@2x.png");
      background-size: 690px 517px;
    }
  }

  .slider__second {
    width: 325px;
    background: url("../img/after-desktop@1x.png");
    top: -190px;
    background-position: right;
    background-size: 690px 517px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/after-desktop@2x.png");
      background-size: 690px 517px;
    }
  }
}
