.main-part {
  height: 480px;
  width: 320px;
  margin-bottom: 22px;
  text-align: center;
  color: @white;
  background: url("../img/index-background-mobile.png");
  background-position: top;
  background-repeat: no-repeat;
  background-size: 320px 305px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/index-background-mobile@2x.png");
  }

  @media (min-resolution: 3dppx) {
    background-image: url("../img/index-background-mobile@3x.png");
  }
}

.main-part__container {
  width: 280px;
  margin: 0 auto;
}

.main-part__name {
  font-size: 36px;
  line-height: 36px;
  font-weight: normal;
  padding-top: 29px;
  margin: 0;
}

.main-part__information {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  margin: 22px 0 29px 0;
}

.main-part__button {
  padding: 11px 0 11px 0;
  margin-bottom: 22px;
}

.main-part__extra {
  display: none;
}

@media (min-width: @tablet-width) {
  .main-part {
    min-height: 790px;
    width: @tablet-width;
    text-align: left;
    color: @black-lighten;
    background: none;
  }

  .main-part__container {
    width: 708px;
  }

  .main-part__name {
    width: 450px;
    font-size: 60px;
    line-height: 60px;
    padding-top: 0;
    margin: 42px 0 42px 89px;
  }

  .main-part__information {
    font-size: 20px;
    line-height: 20px;
    margin: 0 0 20px 89px;
  }

  .main-part__image {
    width: 709px;
    height: 609px;
  }

  .main-part__button {
    width: 245px;
    font-size: 20px;
    line-height: 20px;
    padding: 15px 0 15px 0;
    margin: 0 0 0 89px;
    position: relative;
    top: -584px;
  }
}

@media (min-width: @desktop-width) {
  .main-part {
    width: @desktop-width;
    color: @black-lighten;
    background: none;
    min-height: auto;
    margin-bottom: 74px;
  }

  .main-part__container {
    width: 1220px;
  }

  .main-part__name {
    width: 450px;
    margin: 50px 0 38px 82px;
  }

  .main-part__information {
    margin: 0 0 53px 81px;
  }

  .main-part__image {
    width: 552px;
    height: 499px;
    position: absolute;
    left: 40%;
    top: 142px;
    z-index: 1;
  }

  .main-part__button {
    width: 245px;
    padding: 17px 0 15px 0;
    margin: 0 0 0 81px;
    position: absolute;
    top: 456px;
  }

  .main-part__extra {
    display: block;
    position: absolute;
    left: 50%;
    top: 0;
    z-index: 0;
    height: 696px;
    width: 720px;
    background-color: @leaf;
  }
}
