.option {
  width: 280px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.option__container {
  background-color: #f2f2f2;
  padding: 22px 21px;
  width: 100%;
  margin-bottom: 20px;
  margin-top: 0;
}

.option__title {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.option__name {
  font-size: 24px;
  line-height: 37px;
  text-align: left;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  font-weight: normal;
}

.option__description {
  font-family: @arial;
  font-size: 14px;
  line-height: 18px;
  text-align: left;
  color: @gray;
  margin: 0;
  padding: 0 0 23px 0;
  border-bottom: 1px solid @white-gray;
}

.option__link {
  font-size: 16px;
  line-height: 16px;
  text-align: left;
  text-transform: uppercase;
  display: block;
  margin-top: 18px;
  color: @black-lighten;
  position: relative;
}

.option__link:hover::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 97px;
  width: 34px;
  height: 12px;
  background-image: url("../img/icon-right-arrow-verylong.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.option__link::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 95px;
  width: 25px;
  height: 12px;
  background-image: url("../img/icon-right-arrow-long.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.option__link:active::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 97px;
  width: 34px;
  height: 12px;
  background-image: url("../img/icon-right-arrow-verylong.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  opacity: 0.3;
}

.option__image {
  width: 50px;
  height: 50px;
  background-color: @leaf;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
}

.option__icon {
  position: relative;
}

@media (min-width: @tablet-width) {
  .option {
    width: 708px;
    margin-bottom: 15px;
  }

  .option__container {
    background-color: #f2f2f2;
    padding: 75px 62px 62px 52px;
    width: 100%;
    margin-bottom: 30px;
    position: relative;
  }

  .option__title {
    position: relative;
    margin-bottom: 26px;
  }

  .option__name {
    font-size: 36px;
  }

  .option__description {
    width: 375px;
    font-size: 16px;
    line-height: 24px;
    padding: 0;
    border-bottom: none;
    margin: 0;
    margin-bottom: 56px;
  }

  .option__description--another {
    width: 342px;
  }

  .option__link {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 0;
  }

  .option__link::after {
    top: 4px;
    left: 131px;
  }

  .option__link:hover::after {
    top: 4px;
    left: 131px;
  }

  .option__link:active::after {
    top: 4px;
    left: 131px;
  }

  .option__image {
    order: 2;
    width: 200px;
    height: 200px;
    margin: 0;
    position: absolute;
    margin-left: 395px;
    margin-top: 0;
    top: 0;
  }

  .option__name--pro p {
    width: 340px;
  }

  .option__icon--first {
    width: 141px;
    height: 194px;
    top: -15px;
  }

  .option__icon--second {
    width: 268px;
    height: 113px;
  }
}

@media (min-width: @desktop-width) {
  .option {
    width: 1220px;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 51px;
  }

  .option__container {
    margin-bottom: 20px;
    width: 570px;
    padding: 45px 56px 62px 52px;
  }

  .option__title {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
  }

  .option__name {
    order: 2;
  }

  .option__image {
    order: 1;
    width: 100px;
    height: 100px;
    margin: 0;
    position: relative;
    margin-right: 64px;
  }

  .option__name--pro {
    padding: 0;
  }

  .option__description {
    width: 100%;
    padding: 0;
    margin-bottom: 32px;
  }

  .option__link {
    margin: 0;
  }

  .option__icon--first {
    width: 71px;
    height: 97px;
    top: -10px;
  }

  .option__icon--second {
    width: 134px;
    height: 57px;
  }
}
