*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-family: @font;
  width: 320px;
  margin: 0 auto;
}

@media (min-width: @tablet-width) {
  body {
    width: @tablet-width;
  }
}

@media (min-width: @desktop-width) {
  body {
    width: @desktop-width;
  }
}

a {
  text-decoration: none;
}

.basic-centering {
  width: 280px;
  margin: 0 auto;
}
