.contacts__description {
  background-color: @white;
  position: relative;
  z-index: 2;
}

.contacts__description--gray {
  background-color: #f2f2f2;
}

.contacts__container {
  width: 280px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding-top: 31px;
  padding-bottom: 26px;
}

.contacts__invite {
  margin: 0;
  text-transform: uppercase;
  color: @black-lighten;
  font-size: 16px;
  line-height: 20px;
  width: 142px;
}

.contacts__adress {
  font-family: @arial;
  margin: 0;
  color: @gray;
  font-size: 14px;
  line-height: 20px;
  width: 140px;
}

.contacts__map {
  position: relative;
  background-color: @white-gray;
}

.contacts__map-link {
  font-size: 0;
  display: block;
}

.contacts__pointer {
  position: absolute;
  top: 139px;
  left: 136px;
}

.contacts__pointer-image {
  width: 55px;
  height: 53px;
}

@media (min-width: @tablet-width) {
  .contacts__description {
    z-index: 2;
  }

  .contacts__container {
    width: 626px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 64px;
    padding-bottom: 62px;
  }

  .contacts__invite {
    font-size: 20px;
    line-height: 25px;
    margin-right: 0;
    width: 165px;
  }

  .contacts__adress {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
    width: 160px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .contacts__map {
    position: relative;
    top: 0;
  }

  .contacts__map-link {
    font-size: 0;
    display: block;
  }

  .contacts__map-image {
    height: 400px;
    width: 768px;
  }

  .contacts__pointer {
    top: 145px;
    left: 335px;
  }

  .contacts__pointer-image {
    width: 113px;
    height: 106px;
  }
}

@media (min-width: @desktop-width) {
  .contacts {
    position: relative;
  }

  .contacts__description {
    width: 570px;
    z-index: 2;
    top: 95px;
    left: 110px;
    position: absolute;
  }

  .contacts__description--gray {
    background-color: @white;
  }

  .contacts__container {
    width: 405px;
    margin-left: 77px;
  }

  .contacts__invite {
    margin-right: 0;
    width: 165px;
  }

  .contacts__adress {
    text-align: left;
    width: 155px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .contacts__map {
    position: relative;
    top: 0;
    z-index: 1;
  }

  .contacts__map-link {
    font-size: 0;
    display: block;
  }

  .contacts__map-image {
    height: 400px;
    width: 1440px;
  }

  .contacts__pointer {
    top: 150px;
    left: 1009px;
  }
}
