.how-it-works {
  margin-bottom: 42px;
}

.how-it-works__container {
  width: 280px;
  margin: 0 auto;
}

.how-it-works__name {
  font-size: 36px;
  line-height: 60px;
  text-align: left;
  margin: 0 0 25px 0;
  font-weight: normal;
}

.how-it-works__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.how-it-works__item {
  font-family: @arial;
  list-style: none;
  font-size: 14px;
  line-height: 18px;
  color: @gray;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}

.how-it-works__image {
  width: 60px;
  height: 60px;
  background-color: @leaf;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.how-it-works__description {
  width: 200px;
  margin-top: 0;
  margin-bottom: 0;
}

@media (min-width: @tablet-width) {
  .how-it-works {
    border-bottom: 1px solid @white-gray;
    margin-bottom: 28px;
  }

  .how-it-works__container {
    width: 708px;
  }

  .how-it-works__name {
    font-size: 60px;
    margin: 0 0 70px 0;
  }

  .how-it-works__list {
    display: flex;
    flex-wrap: wrap;
  }

  .how-it-works__list li:first-child,
  .how-it-works__list li:nth-child(3) {
    margin-right: 140px;
  }

  .how-it-works__list li:nth-child(4),
  .how-it-works__list li:nth-child(3) {
    margin-bottom: 100px;
  }

  .how-it-works__item {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 130px;
    display: flex;
    flex-direction: column;
  }

  .how-it-works__image {
    width: 80px;
    height: 80px;
    margin-right: 0;
    margin-bottom: 35px;
  }

  .how-it-works__description {
    width: 245px;
    position: relative;
    z-index: 1;
  }

  .how-it-works__description::after {
    position: absolute;
    font-size: 281px;
    line-height: 281px;
    font-family: @font;
    color: #f2f2f2;
    top: -155px;
    z-index: -1;
    right: 0;
  }

  .how-it-works__description--first::after {
    content: "1";
    right: 11px;
  }

  .how-it-works__description--second::after {
    content: "2";
  }

  .how-it-works__description--third::after {
    content: "3";
  }

  .how-it-works__description--fourth::after {
    content: "4";
  }

  .how-it-works__description--second,
  .how-it-works__description--first,
  .how-it-works__description--third,
  .how-it-works__description--fourth {
    position: relative;
    z-index: 10;
  }
}

@media (min-width: @desktop-width) {
  .how-it-works {
    border-bottom: none;
    margin-bottom: 19px;
  }

  .how-it-works__container {
    width: 1220px;
  }

  .how-it-works__name {
    margin: 0 0 74px 0;
  }

  .how-it-works__list {
    justify-content: space-between;
    flex-wrap: nowrap;
  }

  .how-it-works__list li:last-child {
    width: 242px;
  }

  .how-it-works__list li:first-child {
    width: 245px;
    margin-right: 80px;
  }

  .how-it-works__list li:nth-child(2) {
    width: 220px;
    margin-right: 105px;
  }

  .how-it-works__list li:nth-child(3) {
    width: 245px;
    margin-right: 80px;
  }

  .how-it-works__item {
    display: flex;
    flex-direction: column;
    margin-bottom: 100px;
  }

  .how-it-works__image {
    width: 80px;
    height: 80px;
  }
}
