.catalog {
  width: 320px;
  margin: 0 auto;
}

.catalog-main__name {
  font-size: 36px;
  line-height: 60px;
  text-align: left;
  font-weight: normal;
  margin: 16px 0 28px 20px;
}

.catalog__item {
  padding: 0 20px;
  border-top: 1px solid @white-gray;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.catalog__item picture {
  height: 0;
}


.catalog__list {
  padding: 0;
  margin: 0;
}

.catalog__title {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: left;
  margin: 21px 0 12px 0;
  width: 60%;
}

.catalog__title br {
  display: none;
}

.catalog__table {
  font-size: 12px;
  line-height: 16px;
  font-family: @arial;
  text-align: left;
  color: @gray;
  margin: 0 0 23px 0;
  border: 0;
  border-spacing: 0;
  width: 100%;
}

.catalog__table-item {
  text-align: left;
  padding: 0 60px 0 0;
}

.catalog__table-value {
  text-align: right;
  padding: 0;
}

.catalog__button {
  padding: 10px 0 10px 0;
  font-size: 16px;
  line-height: 20px;
  width: 100%;
  margin-bottom: 24px;
}

.catalog__description {
  width: 140px;
}

.catalog__more {
  border-bottom: 1px solid @white-gray;
}

.catalog__more-title {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: @black-lighten;
  width: 100%;
  margin: 79px 0 9px 0;
  position: relative;
}

.catalog__more-title::before {
  content: "";
  position: absolute;
  top: -53px;
  left: 123px;
  width: 31px;
  height: 31px;
  background-image: url("../img/plus-mobile-svg.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  opacity: 0.2;
}

.catalog__more-description {
  font-family: @arial;
  font-size: 12px;
  line-height: 16px;
  color: @gray;
  width: 100%;
  text-align: center;
  margin: 0 0 20px 0;
}

.catalog__more-button {
  padding: 9px 0 12px 0;
  font-size: 16px;
  line-height: 20px;
  color: @gray;
  background-color: #f2f2f2;
  width: 100%;
  margin-bottom: 22px;
}

.catalog__more-button:hover {
  background-color: @white-gray;
}


@media (min-width: @tablet-width) {
  .catalog {
    width: 708px;
  }

  .catalog-main__name {
    font-size: 60px;
    margin: 43px 0 165px 30px;
  }

  .catalog__item {
    padding: 0 38px;
    width: 324px;
    height: 431px;
    border: none;
    flex-direction: column;
    align-items: center;
    background-color: #f2f2f2;
    margin-bottom: 140px;
    position: relative;
  }

  .catalog__list li:nth-child(7),
  .catalog__list li:nth-child(8) {
    margin-bottom: 66px;
  }

  .catalog__image {
    width: 161px;
    height: 172px;
    position: absolute;
    left: 83px;
    top: -45px;
  }

  .catalog__image--big {
    width: 195px;
    height: 208px;
    left: 68px;
    top: -82px;
  }

  .catalog__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .catalog__title {
    font-size: 20px;
    text-align: center;
    margin: 163px 0 19px 3px;
    width: 100%;
  }

  .catalog__title br {
    display: block;
  }

  .catalog__table {
    width: 250px;
    font-size: 16px;
    margin: 0 0 30px 0;
  }

  .catalog__table-item {
    padding: 8px 0 8px 0;
    border-bottom: 1px solid @white-gray;
    width: 70%;
  }

  .catalog__table-value {
    border-bottom: 1px solid @white-gray;
    width: 30%;
  }

  .catalog__button {
    margin-bottom: 40px;
    padding: 10px 0 10px 2px;
    width: 250px;
  }

  .catalog__description {
    width: 100%;
  }

  .catalog__more {
    border: 1px solid #cdcdcd;
    background-color: @white;
  }

  .catalog__more-title {
    font-size: 20px;
    margin: 164px 0 26px 2px;
    width: 60%;
  }

  .catalog__more-title::before {
    top: -111px;
    left: 45px;
    width: 61px;
    height: 61px;
    background-image: url("../img/plus-svg.svg");
  }

  .catalog__more-description {
    font-size: 16px;
    margin-bottom: 87px;
    width: 70%;
  }

  .catalog__more-button {
    margin-bottom: 40px;
    padding: 10px 0 10px 2px;
    width: 250px;
  }
}

@media (min-width: @desktop-width) {
  .catalog {
    width: 1220px;
  }

  .catalog-main__name {
    margin: 17px 0 100px 112px;
  }

  .catalog__item {
    width: 245px;
    margin-bottom: 77px;
    margin-top: 64px;
  }

  .catalog__table {
    width: 100%;
    margin: 0;
    margin-bottom: 32px;
  }

  .catalog__title {
    margin-bottom: 20px;
  }

  .catalog__table-item {
    width: 60%;
    padding-bottom: 9px;
    padding-top: 6px;
  }

  .catalog__table-value {
    width: 40%;
  }

  .catalog__button {
    padding: 10px 0 10px 2px;
    font-size: 16px;
    line-height: 20px;
    width: 169px;
  }

  .catalog__more {
    border: 1px solid #cdcdcd;
    background-color: @white;
  }

  .catalog__more-title {
    width: 100%;
    margin-bottom: 28px;
  }

  .catalog__more-description {
    width: 100%;
    margin-bottom: 85px;
  }

  .catalog__image {
    left: 44px;
  }

  .catalog__image--big {
    left: 29px;
  }

  .catalog__more-title::before {
    left: 51px;
  }

  .catalog__more-button {
    width: 100%;
  }
}
